@import '../../common/styles/sass/variables';
.skill {
    width: 250px;
    background-color: $bgColor;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid $secondaryColor;
    opacity: 0.9;
    font-family: 'Rambla', sans-serif;
    margin: 5px;
    & h3 {
        font-family: 'Oswald', sans-serif;
        color: $primaryColor;
        letter-spacing: 3px;
        &:hover {
            /*text-transform: uppercase;*/
        }
    }
    .icon {
        position: relative;
        margin-top: 15px;
        height: 60px;
        width: 60px;
        background-color: $textColor;
        opacity: 0.8;
        transform: rotate(45deg);
        display: flex;
        justify-content: center;
        align-items: center;
        & div {
            transform: rotate(-45deg);
            background-size: cover;
        }
    }
    .description {
        padding: 0 5px;
        color: $textColor;
        opacity: 0.9;
        text-align: center;
        margin-bottom: 10px;
    }

}
